import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
      currentSection: "getStartedSection",
      sections: ["getStartedSection", "byThemeSection", "disciplineSection"]
  },
  mutations: {
    getStarted(state) {
      state.currentSection = state.sections[0];
    },
    byTheme(state) {
      state.currentSection = state.sections[1];
    },
    discipline(state) {
      state.currentSection = state.sections[2];
    },
  },
  actions: {

  }
})
